<template>
    <v-layout pa-5 row 
        

        class='white elevation-2' style='border-radius: 25px; text-align: center'>

         <v-snackbar v-model="snackbar.visible" :timeout=2000 :color='snackbar.state ? "success" : "error"'> 
            {{ snackbar.message }} <v-btn text @click="snackbar.visible = false" >Close</v-btn>
        </v-snackbar>
        <v-dialog v-model="dialog" max-width="80%" :key='dialog_key' >
                
                <v-card>
                    <v-card-title>
                    <span class="headline">{{ formTitle }}</span>
                    </v-card-title>


                    <!-- content -->
z
                    <v-card-text>
                        <v-container grid-list-md>
                            <v-layout wrap v-if='userMethod == methodsEnums.PATCH'>

                                <!-- form -->

                                
                                <template v-if='' >
                                    <edit-po
                                        :form.sync='entity'
                                        :products='products'
                                        :refProducts='refProducts'

                                        ref='poRef'
                                        

                                    > </edit-po>

                                </template>

                            

                                
                            </v-layout>

                            <v-layout v-else-if='userMethod == methodsEnums.DELETE' wrap style='background: rgba(0,0,0,.15);border-radius: 12px'>
                                <v-flex    v-for='(item,index) in Object.values(this.entity)' 
                                    style=''
                                    class='pa-2'
                                > {{item}} 
                                </v-flex>

                            </v-layout>
                        </v-container>



                    </v-card-text>
                    
                    <!-- dialog actions -->
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <data-model 
                            :endpoint="endpoint" 
                            
                            @success='postSuccess();' @error='postError()'>
                            <div slot-scope="{ remove, data: post_res, loading : post_loading, create, update, error }">
                                <v-btn color="blue darken-1" text @click="dialogClose"> Cancel </v-btn>
                                <v-btn color="blue darken-1" :loading='post_loading' text @click='dialogSave({functions: {create, remove, update}})'> {{methodToStringPresent(userMethod)}} </v-btn>
                            </div>
                        </data-model>
                    </v-card-actions>
                </v-card>

            </v-dialog>

        <v-flex xs12 
            
        > 
           

            
            

            <!-- <my-form class='pa-5'> </my-form> -->

        </v-flex>

        <v-flex xs12 
            class='printable'
        > 

            <v-layout justify-end >
                <v-btn text rounded @click='userMethod = methodsEnums.PATCH; dialog=true'> EDIT </v-btn>
                <v-btn text rounded @click='userMethod = methodsEnums.DELETE;dialog=true;'> DELETE </v-btn>
            </v-layout>
        </v-flex>

        <v-flex xs12>

            <v-layout justify-center  row wrap class='jobcard-data-fields'
                         >

               <!-- <v-flex xs12 md10> -->



                        <v-flex  xs6 md5 style='border: 1px solid #000; margin-left: -1px'> 

                            <v-layout>
                                <img :src='logo' style='width: 240px'>
                                </img> 

                            </v-layout>
                            
                        </v-flex>

                        <v-flex xs6 md5 pa-5>
                             <h1 class='display-1' style='opacity: .6; text-align: right'> Purchase Order </h1>
                        </v-flex>

                        <v-flex xs6 md5 justify-space-around >
                            <h1 class='title'> Purchase Order ID : <span class='innerValue'> {{entity.po_id}} </span> </h1>
                        </v-flex>

                        <v-flex xs6 md5>
                            <h1 class='title'> Vendor Name : <span class='innerValue'> {{entity.vendor_name}} </span>  </h1>   
                        </v-flex>

                        <v-flex xs6 md5>
                            <h1 class='title'> Subject : <span class='innerValue'> {{entity.subject}} </span> </h1>
                        </v-flex>

                        <v-flex xs6 md5>
                            <h1 class='title'> Date : <span class='innerValue'> {{entity.date | formatDate}} </span> </h1>
                        </v-flex>

 

               <!-- </v-flex> -->

                <!-- dynamic part -->

                <v-flex xs12 md10>
                    <h1 class='body-1' style='opacity: 1; text-align: left;'> Products </h1>
                </v-flex>
                

                <v-flex xs12 md10 pa-0 my-0 style=''  
                    :stsyle='style.parent'
                  
                >
                    
                  <!--  <v-layout justify-center>

                        <v-flex xs12 > -->
                            <v-simple-table class='py-0 ptable'>
                                <thead>
                        
                                    <th> Product Name </th>
                                    
                                    <th> Quantity </th>
                                    <th> Avg Rate </th>
                                    <th> Delivered </th>

                                    <th> Project Address </th>
                                    

                                
                                </thead>

                                <tbody>
                                    <tr v-for=' (i) in entity.products'> 
                                        
                                        <td > {{ i.product_name  }} </td>
                                        
                                        <td> {{i.quantity}} </td>
                                        <td> {{i.avg_rate}} </td>
                                        <td> {{i.delivered}} </td>
                                        <td > {{ i.site_address  }} </td>

                                    </tr>
                                </tbody>
                            </v-simple-table>
                    <!--    </v-flex>

                    </v-layout> -->


                    

                </v-flex>


                <v-flex xs12 md10>
                    <h1 class='subtitle-1' style='opacity: 1; text-align: left;'> Terms Of Service </h1>
                </v-flex>
                        <v-flex xs6 md5>
                            <h1 class='title'> Payment : <span class='innerValue'> {{entity.tac.payment}} </span> </h1>
                        </v-flex>
                        <v-flex xs6 md5>
                            <h1 class='title'> Remarks : <span class='innerValue'> {{entity.tac.remarks}} </span> </h1>
                        </v-flex>

                        <v-flex xs6 md5>
                            <h1 class='title'> Tax Type : <span class='innerValue'> {{entity.tac.tax_type}} </span> </h1>
                        </v-flex>


                        <v-flex xs6 md5>
                            <h1 class='title'> Warranty : <span class='innerValue'> {{entity.tac.warranty}} </span> </h1>
                        </v-flex>

                        <v-flex xs6 md5>
                            <h1 class='title'> Tax Price : <span class='innerValue'> {{entity.tac.tax_price}} </span> </h1>
                        </v-flex>

                        <v-flex xs6 md5>
                            <h1 class='title'> Freight Tax : <span class='innerValue'> {{entity.tac.freight_tax}} </span> </h1>
                        </v-flex>

                        <v-flex xs12 md10>
                            <h1 class='title'> Delivery Period : <span class='innerValue'> {{entity.tac.delivery_period}} </span> </h1>
                        </v-flex>
                    
                

            </v-layout>

        </v-flex>
        
    </v-layout>
</template>

<script>



const methodsEnums = {
    GET : 0,
    POST : 1,
    PATCH : 2,
    DELETE : 3
}

import utilities from '@/components/mixins/utilities.js'
import DataList from '@/components/data/DataList'
import DataModel from '@/components/data/DataModel'
// import axios from 'axios'



// custom

import EditPo from '@/components/newForms/edit_po'
import _ from 'lodash'

import moment from 'moment'

Object.freeze(methodsEnums)

export default {
    extends: utilities,

    components: {

        DataList,
        DataModel,
        
        EditPo
    },

    props: {

    },


    data(){
        return {
            
            logo: require('../../public/logo.png') ,

            style: {
                parent: {
                    background : '#F4F5F6',
                    borderRadius : '15px'
                },

                heading: {
                    opacity: '.7'
                },

                subheading: {
                    opacity: '.5'
                }
            },


            entity: {},
            
            state: {
                error: false
            },

            /* 
             form related
            */

           products: [],
           refProducts : [],
            endpoint: 'purchase-orders' ,

            userMethod : 0,

            dialog: false,
            dialog_key: 555,
            dl_key: 55,
            dialog_loading: false,

            methodsEnums : methodsEnums,

            snackbar: {
                    visible: false,
                    state: false,
                    message : ''

                },

             baseUrl: 'http://34.83.223.211/api/v1/'


            

            


        }
    },

    computed: {

        formTitle(){

        }

    },

    



    async created(){

        try{
             let r = await axios({
                method: 'GET',
                baseURL: this.$config.baseUrl,
                url: 'purchase-orders',
                params: {
                    po_id : this.$route.params.id
                },
                withCredentials: true
            })

            if(r)
            {
                //alert(JSON.stringify(r.data))
                this.entity = Object.assign({},r.data.result[0])
                this.entity.products = JSON.parse(this.entity.products)

                if(!this.entity.products)
                    this.entity.products = []

                // dirty patch

        



            }
                

        }

        catch(e){

            if(e)
            {
                this.state.error = true
            }

        }

        let products = await axios.get(this.baseUrl+'products',{
            withCredentials: true
        })
        this.products = this.generateItems(products.data.results,'product_id')

        if(products)
        {
            this.refProducts = this.productsToObj(products.data.results)
        }

        
       


    },
    mounted(){

        // dirty patch

            

            
        
                this.entity.project_id = Number(this.entity.project_id)
                this.entity.person_id = Number(this.entity.person_id)

               
    },


    methods: {

        productsToObj(input){

            let temp = {}
            Object.keys(input).forEach( (k,i,a) => {
                temp[Number(input[k].product_id)] = input[k]
            } )
            return temp

        },

        generateItems(input,key){
            let temp = []
            input.forEach( (item,index) => {
                temp.push({
                    text: item.name,
                    value: Number(item[key])
                    })
            } )

            return temp
        },

        dialogOpen(){
            this.dialog = true
            this.userMethod = this.methodsEnums.PATCH
        },
        dialogClose(){
            this.dialog = false
        },

        dialogSave (dialog) {
            
            let temp = _.cloneDeep(this.entity)
            temp.products = JSON.stringify(temp.products)
            
            if(temp.tac.freight_tax == undefined || temp.tac.freight_tax == '' || temp.tac.freight_tax == null)
            {
                delete temp.tac.freight_tax

            }

            temp.tac = JSON.stringify(temp.tac)
        

      // alert(JSON.stringify(temp))
        
           switch(this.userMethod)
           {
                case this.methodsEnums.POST :  
                       this.$refs.poRef.$validator.validateAll().then( (r) => {
                            if(r)
                            {
                                dialog.functions.create(temp);
                            }  
                                
                        } );
                        break;
                case this.methodsEnums.PATCH :  
                       // alert('in Patch')
                        
                        this.$refs.poRef.$validator.validateAll().then( (r) => {
                            if(r)
                            {
                         //       alert('in r')
                                dialog.functions.update(temp);
                            }
                
                        } );
                        break;
                case this.methodsEnums.DELETE :  dialog.functions.remove( { po_id: this.entity.po_id } ); break;

        
           }
           
            
            //this.dialogClose()
        },

        postSuccess(){
        
            this.snackbar.message = `Successfully ${this.methodToString(this.userMethod)} a project`
            this.snackbar.state = true
            this.snackbar.visible = true

            ++this.dl_key
            this.dialogClose()

            this.reloadData()
        },


        postError(){

            this.snackbar.state = false
            this.snackbar.visible = true
            this.snackbar.message = 'Something Went Wrong. Try again later' 

        },

        async   reloadData(){

            let r = await axios({
                method: 'GET',
                baseURL: this.$config.baseUrl,
                url: 'purchase-orders',
                params: {
                    po_id : this.$route.params.id
                },
                withCredentials: true
            })

            if(r)
            {
                //alert(JSON.stringify(r.data))
                this.entity = Object.assign({},r.data.result[0])
                this.entity.products = JSON.parse(this.entity.products)

            }
        },

        //extras

        methodToString(choice){
                switch(choice)
                {
                    case this.methodsEnums.POST : return 'added';
                    case this.methodsEnums.PATCH : return 'updated';
                    case this.methodsEnums.DELETE : return 'deleted';
                }
            },
            methodToStringPresent(choice){
            switch(choice)
            {
                case this.methodsEnums.POST : return 'add';
                case this.methodsEnums.PATCH : return 'update';
                case this.methodsEnums.DELETE : return 'delete';
            }
        }
    }



}
</script>

<style>



    .jobcard-data-fields {
        text-align: left;
    }



    .jobcard-data-fields > * > h1{
        font-weight: 400 !important;
    }


    .innerValue{
        opacity: .95 !important;
    }

    .jobcard-data-fields > div{
        margin-left: -1px;
         margin-top: -1px;
        
        padding: 2px 0px 2px 12px;
        
        border: 1px solid #000;
    }


    .ptable  {
        margin-left: -1px;
         margin-top: -1px;
        padding: 0px;
        
        border: 1px solid #000;

    }

    tbody > tr:nth-child(odd){
    background: #fff !important;
   
    }

    @media print {

        bosdy {
            display:table !important; 
            table-layout:fixed !important;
            padding-top:2.5cm !important;
            padding-bottom:2.5cm !important;
            height:auto !important;
        }

        *,h1,h2,h3,h4,h5,h6,span {
            font-size: 12px !important;
        }
        
        .main {
            border-radius: 0px !important;
            box-shadow: 0px !important; 
        }

        .btns, .toHide , .navbar { 
             display: none !important
        }   

        .printable{

            padding: 24px 36px 24px 36px;

        }


        .pes    rProduct {
            page-break-inside: avoid !important
        }

        
         
        
        
    }
    @page { size: auto;  margin: 10mm; }

    @page :first
    {
        margin: 0mm !important;
        size: auto;
    }


</style>
